  body.loaded {
    opacity: 1;
    transition: opacity 0.345s ease-in; }
  body.sc-no-scroll {
    overflow: hidden; }
  body ol.menu {
    display: grid;
    align-items: center;
    justify-content: center;
    grid-column-gap: 32px;
    grid-row-gap: 0px;
    grid-template-columns: repeat(2, calc(50% - 16px));
    list-style: none;
    font-size: 18px;
    text-transform: uppercase;
    text-align: left;
    margin: 0;
    padding: 16px 0 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1; }
    body ol.menu li {
      position: relative;
      width: 100%;
      padding: 0;
      margin: 0;
      height: 100%;
      color: #fff;
      text-shadow: 1px 1px 1px rgba(89, 89, 93, 0.77), 1px -1px 1px rgba(89, 89, 93, 0.66), -1px 1px 1px rgba(89, 89, 93, 0.66), -1px -1px 1px rgba(89, 89, 93, 0.66), 1px 0px 1px rgba(89, 89, 93, 0.66), 0px 1px 1px rgba(89, 89, 93, 0.66), -1px 0px 1px rgba(89, 89, 93, 0.66), 0px -1px 1px rgba(89, 89, 93, 0.66);
      cursor: pointer;
      transition: all 0.444s ease-out;
      height: 100%; }
      body ol.menu li span {
        display: inline-block;
        /* border: 1px dotted #fff4f430; */ }
      body ol.menu li:nth-child(odd) {
        text-align: right; }
      body ol.menu li:hover {
        transform: scale(1.2);
        transition: all 0.333s ease-in; }
  body footer {
    background: #97aead70;
    color: #fff;
    box-shadow: 0 0 11px 0px #00000087;
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: auto;
    text-align: center;
    color: #fff; }
  body h-scroller {
    display: grid;
    width: 100vw;
    grid-template-columns: 100%;
    z-index: 0; }
    body h-scroller section {
      position: relative;
      margin: 0;
      padding: 0;
      width: 100vw;
      min-height: 100vh;
      display: grid;
      align-items: center;
      justify-content: center;
      background: linear-gradient(to top, #1849ac 0%, #092c44 75%); }
      body h-scroller section.start {
        background: url(/media/me1.jpg);
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat; }
      body h-scroller section.common {
        padding: 8px; }

